.regular {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.italic {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-style: italic;
}

.bold {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.semibold {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.champ {
  font-family: 'Champagne & Limousines Bold';
  font-weight: normal;
}

.proxi {
  font-family: 'ProximaNova-Extrabld';
  font-weight: normal;
}

.script {
  font-family: 'Freestyle Script Normal', arial;
  font-weight: normal;
}

section#nav {
  min-height: 100px;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
}

section#nav .wrapper {
  display: flex;
  justify-content: space-between;
}

section#nav .logo {
  margin-top: 10px;
}

section#nav .logo a {
  text-decoration: none;
}

section#nav .logo p {
  text-align: center;
  color: #5363db;
  font-size: 2.4rem;
  font-family: 'Champagne & Limousines Bold';
  font-weight: normal;
}

section#nav .navigation ul li {
  list-style-type: none;
  display: inline-block;
  padding: 20px;
  padding-top: 37px;
}

section#nav .navigation a {
  color: #555555;
  font-size: 1.4rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  text-decoration: none;
}

section#nav .navigation a:hover {
  transition: 0.3s;
  color: #5363db;
}

section#nav .navigation a:hover:after {
  content: "";
  background-color: #5363db;
  display: block;
  width: 2.5rem;
  height: 1px;
  margin: 2px auto;
  transition: 0.3s;
}

section#nav .nav_min {
  display: none;
}

/*==========  Desktop First Method  ==========*/

/*==========  Desktop First Method  ==========*/

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

html {
  font-size: 10px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media (min-width: 0px) and (max-width: 655px) {
  section#nav {
    min-height: 55px;
  }

  section#nav .logo {
    display: block;
  }

  section#nav .navigation {
    display: none;
  }

  section#nav .nav_min {
    position: absolute;
    display: none;
    text-align: center;
  }

  section#nav .nav_min ul li {
    padding-top: 10px;
  }

  section#nav .nav_min a {
    color: #555555;
    font-size: 1.4rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    text-decoration: none;
  }

  section#nav .nav_min a:hover {
    transition: 0.3s;
    background-color: #5363db;
  }
}